.catalog-main {
  @media (min-width: @tablet-width) {
    margin-top: -57px;
    border-top: 1px solid #dfdfdf;
  }
}

.catalog-main__heading {
  font-size: 27px;
  line-height: 30px;
  font-weight: bold;

  margin: 0;
  margin-bottom: 30px;
  padding: 70px 0 78px;
  text-align: center;
  background-color: @bg-gray;

  @media (min-width: @tablet-width) {
    font-size: 43px;
    line-height: 50px;
    margin-bottom: 81px;
    padding-top: 95px;
  }

  @media (min-width: @desktop-width) {
    font-size: 53px;
    line-height: 57px;
    margin-bottom: 133px;
    padding: 92px 0 100px;
  }
}
